    font-family: 'Montserrat';
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format('truetype');
    font-style: normal;
} 

@font-face {
    font-family: 'Atkinson Hyperlegible Next';
    src: url(${___CSS_LOADER_URL_REPLACEMENT_1___}) format('truetype');
    font-style: normal;
}

@font-face {
    font-family: 'Roboto';
    src: url(${___CSS_LOADER_URL_REPLACEMENT_2___}) format('truetype');
    font-style: normal;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Montserrat', 'Atkinson Hyperlegible Next', 'Roboto';
}

.board {
    display: grid;
    grid-template-columns: repeat(10, 35px);
    grid-template-rows: repeat(10, 35px);
    gap: 1px;
    padding: 15px;
}

.cell {
    width: 35px;
    height: 35px;
    background-color: #add8e6;
    border: 1px solid #000;
    cursor: pointer;
}

.cell:hover {
    filter: brightness(0.7);
}

.cell.highlighted {
    background-color: rgba(0, 255, 0, 0.5);
}

.hit {
    background-color: rgb(185, 97, 97) !important;
}

.miss {
    background-color: rgb(110, 109, 109) !important;
}

.ship {
    background-color: #3177a3;
}

.title {
    text-align: center;
    color: #216582;
    text-shadow: 2px 3px 5px #216582;
    margin-bottom: 30px;
}

.board-space h1 {
    text-align: center;
    margin-bottom: 10px;
}

#playground {
    display: none;
    margin-bottom: 35px;
}

#btns-container {
    display: flex;
    justify-content: center;
}

.game-btn {
    padding: 10px;
    margin: 0 20px;
    background-color: #0c3b4b;
    color: #ffffff;
    border: none;
    cursor: pointer;
}

#newGame {
    display: none;
}

.orientation-group {
    display: flex;
    gap: 5px;
    margin-bottom: 8px;
}

#game-config {
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-bottom: 35px;
}

#ship-selection {
    padding: 15px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
}

#ship-selection h3 {
    margin-bottom: 15px;
}

.ship-unit {
    display: flex;
    justify-content: start;
    gap: 1px;
    cursor: pointer;
    padding: 5px;
    border: 2px solid #636363;
}

.ship-unit:hover {
    border: 2px solid #4dd6b6;
}

.unit {
    width: 25px;
    height: 25px;
    background-color: #3177a3;
    border: 1px solid #000;
}

.selected .ship-unit {
    border: 2px solid #4dd6b6 !important;
}

.blocked {
    cursor: not-allowed;
    filter: brightness(0.8);
